import React from 'react';
import { ButtonState } from './components.type';
import { getColorPalette } from '../constants';

export default function Button({ title = '', onClick = () => { }, style = {}, textStyle, leftIcon, variant = 'dark' }: ButtonState) {
  return (
    <button
      style={{ userSelect: 'none', ...style }}
      className={`bg-[${getColorPalette(variant).background}] lg:w-[394px] mt-[24px] flex flex-1 items-center py-[12px] lg:py-[20px] rounded-[6px] cursor-pointer hover:drop-shadow-md transition ease-in`}
      onClick={onClick}
    >
      {leftIcon && <img src={leftIcon} className='w-[18px] h-[18px] mx-[14px]' alt='PB' />}
      <p className={`text-[${getColorPalette(variant).text}] font-bold text-[13px]`} style={textStyle}>
        {title}
      </p>
    </button>
  );
}
