import React from 'react'

function ProductServicesCard({ image, title, onClick = () => { }, type = 'card', variant = 'light' }: any) {
  if (type === 'button') {
    if (variant === 'dark') {
      return (
        <button onClick={onClick} className='min-w-[150px] shadow-md max-w-[150px] lg:max-w-[186px] lg:min-w-[186px] lg:max-h-[93.74px] lg:min-h-[93.74px] rounded-md bg-[#C32C2C] flex justify-center items-center cursor-pointer list-none hover:shadow-lg'>
          <img src={image} className='w-[18px] h-[18px] object-contain' alt='PB' />
          <p className={`font-bold text-[12px] px-[5px] text-[#fff] leading-3 text-center tracking-wider`}>{title}</p>
        </button>
      )
    } else {
      return (
        <button onClick={onClick} className='group transition-[background] ease-in-out duration-[1000ms] border-solid border-[1px] hover:border-0 border-[#f2f2f2] shadow-md min-w-[150px] shadow-md max-w-[150px] lg:max-w-[186px] lg:min-w-[186px] lg:max-h-[93.74px] lg:min-h-[93.74px] rounded-md hover:bg-[#C32C2C] relative flex justify-center items-center cursor-pointer list-none hover:shadow-lg'>
          <img src={image} className='group-hover:hidden w-[18px] h-[18px] object-contain' alt='PB' />
          <p className={`font-bold text-[12px] px-[5px] group-hover:text-[#fff] text-[#C32C2C] leading-3 text-center tracking-wider`}>{title}</p>
        </button>
      )
    }
  }
  return (
    <button onClick={onClick} className='group transition-[background] ease-in-out duration-[1000ms] hover:border-solid hover:border-5 hover:border-[#C32C2C] min-w-[150px] shadow-md max-w-[150px] lg:max-w-[186px] lg:min-w-[186px] lg:max-h-[93.74px] lg:min-h-[93.74px] rounded-md hover:bg-[#C32C2C] relative justify-center items-center cursor-pointer list-none hover:shadow-lg'>
      <img src={image} className='group-hover:hidden object-cover h-full w-full rounded-md' alt='PB' />
      <div className='absolute flex flex-1 bottom-0 left-0 w-full py-1 rounded-b-md justify-center items-center group-hover:h-full group-hover:rounded-t-md :transition ease-in-out bg-[#C32C2C] group-hover:bg-transparent'>
        <p className={`text-[9px] lg:text-[12px] text-white text-center font-[500] tracking-wider`}>{title}</p>
      </div>
    </button>
  )
}

export default ProductServicesCard