import html2canvas from "html2canvas";
import { CardInfoState, CardDataObjectState } from "../components/components.type";

export const convertToObject = (cardData: CardDataObjectState[]) => {
  const cardInfo: CardInfoState = {};
  cardData?.forEach((item: CardDataObjectState) => {
    cardInfo[item.key] = item.value
  });
  return cardInfo;
};

export const captureImage = async () => {
  const canvas = await html2canvas(document.querySelector("#capture"));
  const image = new Image();
  image.src = canvas.toDataURL('image/jpeg');
  let base64ImageData = image.src.replace('data:image/jpeg;base64,', '')
  return base64ImageData
};

export const downloadImage = (screenCapture: string) => {
  const byteCharacters = atob(screenCapture);
  const byteNumbers = new Array(byteCharacters.length);
  for (let i = 0; i < byteCharacters.length; i++) {
    byteNumbers[i] = byteCharacters.charCodeAt(i);
  }
  const byteArray = new Uint8Array(byteNumbers);
  const blob = new Blob([byteArray], { type: 'image/png' });

  const downloadLink = document.createElement('a');
  const url = URL.createObjectURL(blob);
  downloadLink.href = url;
  downloadLink.download = 'image.png';
  document.body.appendChild(downloadLink);
  downloadLink.click();

  document.body.removeChild(downloadLink);
  URL.revokeObjectURL(url);
};

export const handleThunkResponse = ({ result, rejectWithValue }) => {
  result.originalError = null;
  result.config = null;

  if (result.ok) {
    //* return problem if status is E *//
    if (result.data?.status === 'E') {
      return {
        ...result,
        problem: result.data?.message,
      };
    }
    return result;
  }

  //* Return problem is http error *//
  const problem =
    result?.data?.error ?? result.problem ?? 'Unable to connect to server.';
  result.problem = problem;

  return rejectWithValue ? rejectWithValue(result) : result;
};