export const colors = {
  primary: '#C32C2C',
  white: '#fff',
  black: '#000',
}
export const greenGradient = ['#095252', '#058a8a', '#00C2C2'];
export const blueGradient = ['#C32C2C', '#c22e27', '#0008C2'];
export const getColorPalette = (variant: 'light' | 'grey' | 'dark') => {
  switch (variant) {
    case 'light':
      return {
        background: '#fff',
        text: '#C32C2C'
      }
    case 'grey':
      return {
        background: '#C7C7C7',
        text: '#fff'
      }
    default:
      return {
        background: '#C32C2C',
        text: '#fff'
      }
  }
}