import React, { useRef, useState } from 'react';
import CardList from './CardList';
import { eBizData, eBizDataError, eBizDataLoading } from '../store/contactSlice';
import { useSelector } from 'react-redux';
import '../App.css';
import Flippy, { FrontSide, BackSide } from 'react-flippy';
import { CardVarient } from '../constants';
import { convertToObject } from '../utils';
import Button from './Button';
import {
  ReloadDarkIcon,
  ReloadSuccessDarkIcon,
  ReloadLightIcon,
  OfficeAddressIcon,
  OfficeNumberIcon,
  FaxNumberIcon,
  MobileNumberIcon,
  EmailIcon,
  CheckIcon,
  SaveIcon,
  PBTextLogo
} from '../assets/images'

export default function Card({ downloadImage = () => { }, expired = false }) {
  const cardFlippyRef = useRef(null)
  const cardInfo = useSelector(eBizData);
  const type = cardInfo.find((item) => item.key === 'cardType');
  const cardDesign = CardVarient.find((item) => type?.value === item.type) || CardVarient[0];
  const contactData = convertToObject(cardInfo);
  const isDataLoading = useSelector(eBizDataLoading);
  const isDataError = useSelector(eBizDataError);
  const [isRequested, setIsRequested] = useState(false);

  const imageStyle = {
    filter: cardDesign?.type === 'type3' || cardDesign?.type === 'type5' || cardDesign?.type === 'type4' ? ` invert(99%) sepia(55%) saturate(0%) hue-rotate(120deg) brightness(192%) contrast(100%)` : `hue-rotate(0deg)`,
  };

  if (isDataLoading) {
    return <Flippy ref={cardFlippyRef} flipOnHover={false} flipOnClick={false} flipDirection='horizontal' className='rounded-[10px] px-[26px]'>
      <FrontSide
        style={{
          background: cardDesign?.isGradients ? `linear-gradient(to right, ${cardDesign?.bgColor})` : cardDesign?.bgColor,
          flex: 1,
        }}
        className='sm:min-w-full sm:aspect-[1.74] min-h-[195px] lg:min-w-[394px] lg:min-h-[225px] flex flex-col justify-center items-center'>
        <div id='capture'>
          <svg aria-hidden="true" className="w-8 h-8 text-gray-200 animate-spin fill-[#c32c2c]" viewBox="0 0 100 101" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z" fill="currentColor" />
            <path d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z" fill="currentFill" />
          </svg>
          <span className="sr-only">Loading...</span>
        </div>
      </FrontSide>
    </Flippy>
  }

  const renderCardErrors = () => {
    if (isRequested) {
      return (
        <>
          <img src={ReloadSuccessDarkIcon} className='w-[40px] h-[40px] object-contain' alt='PB' />
          <p className='text-center text-[10px] lg:text-[14px] mt-[24px] text-[#000]'>View request sent. You will be able to view the eBiz Card profile once the owner has approved your request. </p>
        </>
      )
    } else {
      return (
        <>
          <img src={ReloadDarkIcon} className='w-[40px] h-[40px] object-contain' alt='PB' />
          <p className='text-center text-[10px] lg:text-[14px] mt-[24px] text-[#000]'>The eBiz Card profile you are requesting has <span className='text-[#c32c2c] font-bold'>expired</span>. You may send a request to the owner for access to view again.</p>
        </>
      )
    }

  }

  if (isDataError || expired) {
    return (
      <>
        <Flippy ref={cardFlippyRef} flipOnHover={false} flipOnClick={false} flipDirection='horizontal' className='rounded-[10px] px-[26px]'>
          <FrontSide
            style={{
              background: cardDesign?.isGradients ? `linear-gradient(to right, ${cardDesign?.bgColor})` : cardDesign?.bgColor,
              flex: 1,
            }}
            className='sm:min-w-full sm:aspect-[1.74] min-h-[195px] lg:min-w-[394px] lg:max-w-[394px] lg:min-h-[225px] flex flex-col justify-between'>
            <div id='capture' className='flex-1 flex flex-col justify-center items-center px-11'>
              {renderCardErrors()}
            </div>
          </FrontSide>
        </Flippy>
        <div className='flex flex-1 px-[24px] lg:px-0'>
          {isRequested ? <Button
            leftIcon={CheckIcon}
            title='View Request Sent'
            variant='grey'
          /> : <Button
            leftIcon={ReloadLightIcon}
            title='Send View Request'
            onClick={() => {
              setIsRequested(true)
            }}
          />}
        </div>
      </>
    )
  }
  return (
    <Flippy ref={cardFlippyRef} flipOnHover={false} flipOnClick={false} flipDirection='horizontal' className='rounded-[10px] px-[26px]'>
      <FrontSide
        style={{
          background: cardDesign?.isGradients ? `linear-gradient(to right, ${cardDesign?.bgColor})` : cardDesign?.bgColor,
          flex: 1,
          padding: 0
        }}
        onClick={() => cardFlippyRef.current.toggle()}
        className='sm:min-w-full sm:aspect-[1.74] min-h-[195px] lg:min-w-[394px] lg:min-h-[225px] flex flex-col justify-between'>
        <div id='capture' className='p-[1em] flex flex-1 flex-col justify-between'>
          <div className='flex'>
            <div className='flex flex-1 items-center justify-end'>
              <div className='flex flex-col mr-[8px]'>
                <p style={{ color: cardDesign?.textColor }} className='text-right text-[14px] font-bold'>
                  {contactData.name?.toUpperCase()}
                </p>
                <span style={{ color: cardDesign?.textColor }} className='text-right text-[10.5px] mt-[-1px]'>
                  {contactData.designation}
                </span>
              </div>
            </div>
          </div>
          <div>
            <div className='flex items-center'>
              <img alt='PB' src={cardDesign?.pbLogo} style={imageStyle} className='w-[41px] h-[52px]' />
              <div className='flex flex-col ml-4'>
                <img alt='PB' src={PBTextLogo} style={imageStyle} className='h-[12px]' />
                <p className='text-[8px] mt-1'>(5463-H)</p>
              </div>
            </div>
            <div className='flex flex-col mt-1 ml-14'>
              <div className='w-[220px]'>
                <CardList title={contactData.branchName?.toUpperCase()} numberOfLines={4} cardDesign={cardDesign} style={{ fontWeight: 'bold' }} />
                <CardList image={OfficeAddressIcon} title={contactData.branchAddress} numberOfLines={4} cardDesign={cardDesign} />
                <CardList image={OfficeNumberIcon} title={contactData.branchTel} cardDesign={cardDesign} />
                <CardList image={FaxNumberIcon} title={contactData.branchFax} cardDesign={cardDesign} />
                <CardList image={MobileNumberIcon} title={contactData.mobileNumber} cardDesign={cardDesign} />
                <CardList image={EmailIcon} title={contactData.email} cardDesign={cardDesign} />
              </div>
            </div>
          </div>
        </div>
      </FrontSide>
      <BackSide
        style={{
          background: cardDesign?.isGradients ? `linear-gradient(to right, ${cardDesign?.bgColor})` : cardDesign?.bgColor,
        }}
        className='flex'>
        <div className='flex-1 flex justify-center items-center'>
          <button onClick={() => cardFlippyRef.current.toggle()} className='absolute w-full h-full flex-1 z-[1]'></button>
          <button
            onClick={() => downloadImage()}
            className='bg-[#C32C2C] rounded-[4px] w-[60%] flex justify-center items-center py-1 z-[2]'>
            <img alt='Save Card' src={SaveIcon} className='w-[14px] mr-2' />
            <p className='text-white text-[11px] font-bold'>Save Card Image</p>
          </button>
        </div>
      </BackSide>
    </Flippy>
  );
}
