import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';

import { RootState } from '.';
import { EBizCardData } from '../data';
import { API } from '../constants';
import axios from 'axios';
export interface ContactObjectState {
  name?: string;
  company?: string;
  title?: string;
  officeNumber?: string;
  mobileNumber?: string;
  email?: string;
  officeAddress?: string;
  notes?: string;
  designation?: string;
  branchName?: string;
}
export interface CardDataObjectState {
  key?: string;
  label?: string;
  value?: string;
}
export interface ContactState {
  ebizData: any;
  loading: boolean;
  error: boolean;
  errorStatus: any
}

export const fetchEbizData = createAsyncThunk(
  'fetchEbizData',
  async (request: string, { getState, rejectWithValue }) => {
    try {
      if (request === undefined || request === null) {
        return rejectWithValue('Failed to fetch data');
      } else if (request === 'demo') {
        return EBizCardData.cardData;
      } else {
        const result = await axios.get(API + `/EBiz/web/${request}`, {
          headers: {
            'Access-Control-Allow-Origin': '*'
          }
        });
        console.log('res1', result);
        return result;
      }
    } catch (error) {
      console.log('error1', error);
      return rejectWithValue(error.message || 'Failed to fetch data');
    }
  }
);

export const fetchEbizPreviewData = createAsyncThunk(
  'fetchEbizPreviewData',
  async (request: string, { getState, rejectWithValue }) => {
    try {
      if (request === 'demo') {
        return EBizCardData.cardData;
      } else {
        const result = await fetch(API + `/EBiz/data/preview`, {
          method: 'POST',
          headers: {
            'Access-Control-Allow-Origin': '*'
          },
          body: JSON.stringify({
            adId: request,
          }),
        })
          .then(response => response.json())
          .then(json => {
            return json;
          })
          .catch(error => {
            console.error(error);
          });
        return { data: result };
      }
    } catch (error) {
      console.log('error1', error);
      return rejectWithValue(error.message || 'Failed to fetch data');
    }
  }
);

const initialState: ContactState = {
  ebizData: [],
  loading: true,
  error: false,
  errorStatus: null
};

export const ContactSlice = createSlice({
  name: 'contact',
  initialState,
  reducers: {
    setEbizData: (state, { payload }) => {
      state.ebizData = payload
    },
    setLoading: (state, { payload }) => {
      state.loading = false
    },
  },
  extraReducers: (builder) => {
    builder.addCase(fetchEbizData.rejected, (state, { payload }) => {
      state.loading = false;
      state.ebizData = [];
      console.log('payload', payload);

      state.error = true;
      state.errorStatus = payload
    });
    builder.addCase(fetchEbizData.pending, (state, { payload }) => {
      state.loading = true;
    });
    builder.addCase(fetchEbizData.fulfilled, (state, { payload }) => {
      state.loading = false;
      console.log('payload', payload);
      state.ebizData = payload.data;
    });
    builder.addCase(fetchEbizPreviewData.rejected, (state, { payload }) => {
      state.loading = false;
      state.ebizData = [];

      state.error = true;
      state.errorStatus = payload
    });
    builder.addCase(fetchEbizPreviewData.pending, (state, { payload }) => {
      state.loading = true;
    });
    builder.addCase(fetchEbizPreviewData.fulfilled, (state, { payload }) => {
      state.loading = false;
      state.ebizData = payload?.data ?? [];
    });
  },
});

export const { setEbizData, setLoading } = ContactSlice.actions;

export const eBizData = (state: RootState) => state.contact.ebizData;
export const eBizDataLoading = (state: RootState) => state.contact.loading;
export const eBizDataError = (state: RootState) => state.contact.error;
export const eBizDataErrorStatus = (state: RootState) => state.contact.errorStatus;

export default ContactSlice.reducer;
