import { CardDataObjectState } from "../components/components.type";

function jsonToVCF(data: CardDataObjectState[]) {
  let vcfString = "BEGIN:VCARD\nVERSION:3.0\n";
  let branchName = data.find((item) => item.key === 'branchName')
  data.forEach(item => {
    if (item.value === null) {
      return
    }
    switch (item.key) {
      case 'card':
        vcfString += `PHOTO;TYPE=JPEG;ENCODING=b:${item.value}\n`;
        break;
      case 'name':
        vcfString += `N:${item.value}\n`;
        break;
      case 'company':
        vcfString += `ORG:${item.value}\n`;
        break;
      case 'designation':
        vcfString += `TITLE:${item.value}\n`;
        break;
      case 'branchTel': {
        const officeNumbers = item.value.replaceAll(' ', '').split(',');
        officeNumbers.forEach(number => {
          vcfString += `TEL;TYPE=work:${number}\n`;
        });
        break;
      }
      case 'branchFax': {
        const faxNumbers = item.value.replaceAll(' ', '').split(',');
        faxNumbers.forEach(number => {
          vcfString += `TEL;TYPE=fax:${number}\n`;
        });
        break;
      }
      case 'mobileNumber': {
        const mobileNumbers = item.value.replaceAll(' ', '').split(',');
        mobileNumbers.forEach(number => {
          vcfString += `TEL;TYPE=home:${number}\n`;
        });
        break;
      }
      case 'email':
        vcfString += `EMAIL:${item.value}\n`;
        break;
      case 'branchAddress':
        vcfString += `ADR;TYPE=work;LABEL:${branchName?.value === null ? item.value : branchName?.value + ', ' + item.value}\n`;
        break;
      default:
        break;
    }
  });
  vcfString += "END:VCARD";
  return vcfString;
}

export function downloadVCard(contactData: CardDataObjectState[], screenCapture: string) {
  let addedContactData = [...contactData, {
    key: 'card',
    label: 'Card',
    value: screenCapture
  }]
  const vCardData = jsonToVCF(addedContactData);
  const element = document.createElement('a');
  const file = new Blob([vCardData], { type: 'text/vcard' });
  element.href = URL.createObjectURL(file);
  element.download = 'contact.vcf';
  document.body.appendChild(element);
  element.click();
}

