import React from 'react';
import '../App.css';
function Products({ image, title, onClick = () => { }, type = 'card', variant = 'light' }: any) {
  if (type === 'button') {
    if (variant === 'dark') {
      return (
        <button onClick={onClick} className='flex-none rounded-md relative cursor-pointer list-none bg-[#C32C2C]'>
          <div className='w-[94px] h-[84px] shadow-md lg:shadow-lg lg:w-[186px] lg:h-[93.74px] flex border-[1.12px] border-[#C32C2C] flex-col  rounded-md justify-center items-center  '>
            <div className='flex items-center mb-2'><img src={image} className='w-[18px] h-[18px] object-contain' alt='PB' /></div>
            <p className={`text-[11px] font-bold px-[5px] text-[#fff] leading-3 text-center tracking-wider`}>{title}</p>
          </div>
        </button>
      )
    } else {
      return (
        <button onClick={onClick} className='group flex-none  rounded-md relative cursor-pointer list-none  hover:bg-[#C32C2C] transition ease-in-out duration-[1000ms]'>
          <div className='w-[94px] h-[84px] shadow-md lg:shadow-lg lg:w-[186px] lg:h-[93.74px] flex border-[1.12px] border-[#C32C2C] flex-col  rounded-md justify-center items-center  '>
            <div className='group-hover:hidden flex items-center mb-2'><img src={image} className='w-[18px] h-[18px] object-contain' alt='PB' /></div>
            <p className={`text-[11px] font-bold px-[5px] text-[#C32C2C] group-hover:text-white leading-3 text-center tracking-wider`}>{title}</p>
          </div>
        </button>
      )
    }
  }
  return (
    <button onClick={onClick} className='group flex-none  rounded-md relative cursor-pointer list-none  hover:bg-[#C32C2C] transition ease-in-out duration-[1000ms]'>
      <div className='w-[94px] h-[84px] shadow-md lg:shadow-lg lg:w-[186px] lg:h-[93.74px] flex border-[1.12px] border-[#C32C2C] flex-col  rounded-md justify-center items-center  '>
        <div className='group-hover:hidden flex items-center mb-2'><img src={image} className='w-[18px] h-[18px] object-contain' alt='PB' /></div>
        <p className={`text-[11px] font-bold px-[5px] text-[#C32C2C] group-hover:text-white leading-3 text-center tracking-wider`}>{title}</p>
      </div>
    </button>
  );
}

export default Products;
