import React from 'react';
import BottomList from './BottomList';
import ProductServicesCard from './ProductServicesCard';
import Products from './Products';
import { marketingData } from '../data';

function MarketingLists() {
  const renderCard = (mDData) => {
    switch (mDData.type) {
      case 'bigCard':
        return <ProductServicesCard key={mDData.title} image={mDData.image} title={mDData.title} onClick={mDData.onClick} />

      case 'smallCard':
        return <Products key={mDData.title} image={mDData.icon} title={mDData.title} onClick={mDData.onClick} />

      case 'smallButtonDark':
        return <Products key={mDData.title} image={mDData.icon} title={mDData.title} onClick={mDData.onClick} type='button' variant="dark" />

      case 'smallButtonLight':
        return <Products key={mDData.title} image={mDData.icon} title={mDData.title} onClick={mDData.onClick} type='button' variant="light" />

      case 'bigButtonDark':
        return <ProductServicesCard key={mDData.title} image={mDData.image} title={mDData.title} onClick={mDData.onClick} type='button' variant="dark" />

      case 'bigButtonLight':
        return <ProductServicesCard key={mDData.title} image={mDData.image} title={mDData.title} onClick={mDData.onClick} type='button' variant="light" />

      default:
        break;
    }
  }
  return (
    <div className='w-full lg:max-w-screen-2xl'>
      {marketingData.map((mD) => {
        return (
          <BottomList key={mD.title} image={mD.icon} title={mD.title}>
            {mD.data.map((mDData) => renderCard(mDData))}
          </BottomList>
        )
      })}
    </div>
  );
}

export default MarketingLists;
