import React, { useEffect, useState } from 'react';
import './App.css';
import { useDispatch, useSelector } from 'react-redux';
import ListHeader from './components/ListHeader';
import ContactInformation from './components/ContactInformation';
import MarketingLists from './components/MarketingLists';
import { eBizData, fetchEbizData, fetchEbizPreviewData } from './store/contactSlice';
import Button from './components/Button';
import Card from './components/Card';
import { captureImage, downloadVCard, downloadImage } from './utils';
import { version } from './constants';
import { CardIconWhite, ExportIcon } from './assets/images';

function App() {
  const search = window.location.search;
  const params = new URLSearchParams(search);
  const session = params.get('session');
  const adId = params.get('adid');
  const [screenCapture, setScreenCapture] = useState('');
  const data = useSelector(eBizData);

  const dispatch = useDispatch<any>();

  useEffect(() => {
    getData()
    if (document.querySelector("#capture") !== null) {
      setTimeout(async () => {
        let image = await captureImage()
        setScreenCapture(image);
      }, 2000);
    }
  }, []);

  const getData = async () => {
    if (session === 'preview') {
      await dispatch(fetchEbizPreviewData(adId));
    } else {
      await dispatch(fetchEbizData(session));
    }
  };

  let expired = session === null || session === undefined

  if (expired || data?.length === 0) {
    return (
      <>
        <main className='flex min-h-screen flex-col items-center justify-between bg-[#fff]'>
          <ListHeader title='Public Bank Dynamic eBiz Card Profile' image={CardIconWhite} containerStyle='flex-1' />
          <div className='w-screen sm:max-w-sm lg:max-w-[898px] shadow-sm lg:shadow-none bg-[#fff] mx-auto '>
            <div className='lg:flex-row lg:justify-around lg:flex'>
              <div className='flex flex-col justify-center lg:items-center mt-[12px]'>
                <Card expired={expired === false ? data?.length === 0 : expired} />
              </div>
            </div>
            <div className='flex justify-center mt-[10px] py-[22px]'>
              <MarketingLists />
            </div>
          </div>
          <ListHeader title='Copyright © 2024 Public Bank Berhad 196501000672 (6463-H). All Rights Reserved.' textStyle='!font-[400] !text-[14px] text-center' />
        </main>
        <p className='absolute right-0 bottom-0'>v{version}</p>
      </>
    )
  }

  return (
    <main className='flex min-h-screen flex-col items-center justify-between bg-[#fff]'>
      <ListHeader title='Public Bank Dynamic eBiz Card Profile' image={CardIconWhite} containerStyle='flex-1' />
      <div className='w-screen sm:max-w-sm lg:max-w-[898px] shadow-sm lg:shadow-none bg-[#fff] mx-auto '>
        <div className='lg:flex-row lg:justify-around lg:flex'>
          <div className='flex flex-col justify-center lg:items-center mt-[12px]'>
            <Card downloadImage={() => downloadImage(screenCapture)} />
            <div className='hidden lg:flex lg:flex-col'>
              <Button
                leftIcon={ExportIcon}
                title='Save Contact Info to Phone'
                onClick={async () => {
                  downloadVCard(data, screenCapture)
                }}
              />
            </div>
          </div>
          <div className='flex flex-1 flex-col justify-center mt-[12px] lg:p-0 lg:pr-[60px] px-[24px]'>
            <ContactInformation />
            <div className='flex flex-col lg:hidden lg:flex-col'>
              <Button
                leftIcon={ExportIcon}
                title='Save Contact Info to Phone'
                onClick={async () => {
                  downloadVCard(data, screenCapture)
                }}
              />
              <div className='h-[20px]' />
            </div>
          </div>
        </div>
        <div className='flex justify-center mt-[10px] py-[22px]'>
          <MarketingLists />
        </div>
      </div>
      {/* <div className='relative flex w-full'> */}
      <ListHeader title='Copyright © 2024 Public Bank Berhad 196501000672 (6463-H). All Rights Reserved.' textStyle='!font-[400] !text-[14px] text-center'>
        <p className=' text-white text-[8px] text-right opacity-50 mt-4'>Version: {version}</p>
      </ListHeader>

      {/* </div> */}

    </main>
  );
}

export default App;
